#homePage {
  
    background-color: black;
  

  .navbar-nav {
    font-size: 18px;
    font-weight: 700;
  }
  #navbarNavAltMarkup {
    color: orange;
    font-size: 20px;
  }
  #nav {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 10px;
  }

  #north {
    background-image: url("../logos/vegetable.jpg");
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: center;
  }
  #east {
    background-image: url("../logos/east.jpg");
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: center;
  }
  #west {
    background-image: url("../logos/west.jpg");
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: center;
  }
  #south {
    background-image: url("../logos/south.jpg");
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: center;
  }
  #east,
  #west,
  #south,
  #north {
    width: 100%;
    height: 20vh;
    border: 5px solid;
    border-radius: 10px;
    margin-top: 1rem;
    margin-left: 2px;
    color: white;
    text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
      -1px -1px 0 #000;
      overflow: scroll;
    white-space: nowrap;
  }

  a {
    text-decoration: none;
    color: white;
  }
  @media only screen and (max-width: 600px) {
    section:nth-child(2) input {
      width: 100%;
    } /*targets specific devices with known width */
  }
  @media only screen and (max-width: 400px) {
    h1 {
      font-size: 50%;
    }
  }
  
}
